import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import InfoRow from '@vkontakte/vkui/dist/components/InfoRow/InfoRow';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Progress from '@vkontakte/vkui/dist/components/Progress/Progress';
import Icon32Place from '@vkontakte/icons/dist/32/place';

import './Style.css';


const Play = (props) => {
	const userResponseClick = () => {
		props.userResponse()
	}
	const nextLevelButton = () => {
		props.startGame()
	}


	return (
		<Panel id={props.id} separator={false}>
			<PanelHeader left={<PanelHeaderBack onClick={() => window.history.back()} />}>
				Play
        </PanelHeader>
			<div className="stage">
			
				<h1 style={{ color: "white", fontSize: 45, fontFamily: 'Neucha', textAlign: "center" }}>Какой город <br /> ближе?</h1>
				<Icon32Place className="iconPlace" width={50} height={50} />
				<div className="userCityName"><h1 style={{ color: "white", fontSize: 15, fontFamily: 'Neucha', textAlign: "center" }}>{props.geoUser}</h1></div>
				<Button onClick={() => userResponseClick()} className="cityButtonLeft" size="l" mode="outline" style={{ color: "white", fontFamily: 'Neucha', textAlign: "center" }}><h1>{props.leftCity}<br />{props.leftCityDictance}</h1></Button>
				<Button onClick={() => userResponseClick()} className="cityButtonRight" size="l" mode="outline" style={{ color: "white", fontFamily: 'Neucha', textAlign: "center" }}><h1>{props.rightCity}<br />{props.rightCityDictance}</h1></Button>
				<h1 className="timer">{props.timer}</h1>
				<InfoRow className="progress">
					<Progress style={{ background: "red" }} value={props.count} />
				</InfoRow>
				<Button onClick={() => nextLevelButton()} className="nextLevelButton" size="l" mode="outline" style={{ color: "white", fontFamily: 'Neucha', textAlign: "center" }}><h1>Дальше</h1></Button>
			</div>
		</Panel>
	)
	
};

export default Play;